	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import discussqinshixinxi from '@/views/discussqinshixinxi/list'
	import qinshixinxi from '@/views/qinshixinxi/list'
	import qinshishenqing from '@/views/qinshishenqing/list'
	import yonghuqiandao from '@/views/yonghuqiandao/list'
	import yonghu from '@/views/yonghu/list'
	import messages from '@/views/messages/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '校园资讯',
			component: news
		}
		,{
			path: '/discussqinshixinxi',
			name: '寝室信息评论',
			component: discussqinshixinxi
		}
		,{
			path: '/qinshixinxi',
			name: '寝室信息',
			component: qinshixinxi
		}
		,{
			path: '/qinshishenqing',
			name: '寝室申请',
			component: qinshishenqing
		}
		,{
			path: '/yonghuqiandao',
			name: '用户签到',
			component: yonghuqiandao
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/messages',
			name: '留言板',
			component: messages
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
